.visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;

  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.hidden {
  display: none;
}

.slow-show {
  animation: slow-show 0.5s;
}

.radial-pulse {
  animation: radial-pulse 1.8s infinite;
}
