@font-face {
  font-family: "Appetite New";
  src: url("../fonts/AppetiteNew.woff2") format("woff2"), url("../fonts/AppetiteNew.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Museo Sans Cyrl";
  src: url("../fonts/MUSEOSANSCYRL100.woff2") format("woff2"), url("../fonts/MUSEOSANSCYRL100.woff") format("woff");
  font-weight: 100;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Museo Sans Cyrl";
  src: url("../fonts/MUSEOSANSCYRL300.woff2") format("woff2"), url("../fonts/MUSEOSANSCYRL300.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Museo Sans Cyrl";
  src: url("../fonts/MUSEOSANSCYRL500.woff2") format("woff2"), url("../fonts/MUSEOSANSCYRL500.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Museo Sans Cyrl";
  src: url("../fonts/MUSEOSANSCYRL700.woff2") format("woff2"), url("../fonts/MUSEOSANSCYRL700.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
