  font-display: swap;
}
body {
  margin: 0;
  padding: 0;
  min-width: 320px;
  font-family: "Museo Sans Cyrl", "Arial", sans-serif;
  font-weight: 300;
  color: #282828;
  background-color: #ffffff;
  background-image: url("../img/background-layer.png");
  background-repeat: no-repeat;
  background-size: cover;
}
* {
  box-sizing: border-box;
}
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
a {
  text-decoration: none;
  color: inherit;
}
img {
  vertical-align: bottom;
}
button {
  border: none;
  background-color: transparent;
}
@keyframes radial-pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(240, 151, 155, 0.5);
  }
  100% {
    box-shadow: 0 0 0.625vw 1.302vw rgba(0, 0, 0, 0);
  }
}
@keyframes slow-show {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes cloud-left-show {
  0% {
    transform: translateX(-300%);
  }
  100% {
    transform: translateX(0);
  }
}
@keyframes cloud-right-show {
  0% {
    transform: translateX(300%);
  }
  100% {
    transform: translateX(0);
  }
}
.main {
  position: relative;
  overflow: hidden;
}
.button {
  position: relative;
  width: 19.74vw;
  height: 4.432vw;
  padding-right: 5.99vw;
  font-size: 0.729vw;
  letter-spacing: 0.12em;
  font-weight: 700;
  color: #ffffff;
  text-transform: uppercase;
  border-radius: 43px;
  background-color: #d7747a;
  box-shadow: 0 -0.052vw 0 0 #f8b5b7, 0 0.208vw 0 0 #d7747a, 0 0.365vw 1.406vw 0 rgba(241, 131, 131, 0.57);
  background-image: linear-gradient(to bottom, #f8b5b6 0%, #f2969b 94.12%, #ef8b91 94.12%, #ef8b91 98.82%, #e48086 98.82%);
  outline: none;
  cursor: pointer;
}
.button::after {
  position: absolute;
  content: "";
  top: 0.26vw;
  right: 0;
  transform: translateX(46.5%);
  width: 14.219vw;
  height: 3.854vw;
  background-image: url("../img/Branch.png");
  background-repeat: no-repeat;
  background-size: contain;
}
.button--form {
  width: 25.521vw;
  height: 3.906vw;
  padding-right: 5vw;
  font-size: 0.938vw;
}
.button--form::after {
  top: 0.521vw;
  right: 1.198vw;
  width: 10.365vw;
  height: 2.708vw;
  background-image: url("../img/flower.png");
}
.button:hover {
  opacity: 0.95;
}
.button:active {
  opacity: 1;
}
@media (max-width: 991px) {
  .button {
    font-size: 7px;
  }
  .button::after {
    right: -5px;
  }
}
@media (max-width: 767px) {
  .button {
    width: 80%;
    height: auto;
    padding: 5px;
    font-size: 14px;
  }
  .button::after {
    left: 65%;
    width: 100px;
    height: 50px;
  }
  .button--form {
    width: 100%;
    height: 50px;
  }
  .button--form::after {
    top: 50%;
  }
}
.page-header {
  display: flex;
  flex-direction: column;
  width: 81.25vw;
  margin: 0 auto;
  padding-top: 1.667vw;
}
.page-header__top-wrapper {
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding: 0 0.781vw;
}
@media (max-width: 991px) {
  .page-header {
    width: 98%;
  }
}
@media (max-width: 767px) {
  .page-header {
    flex-direction: column-reverse;
    padding-top: 45px;
  }
  .page-header__top-wrapper {
    flex-direction: column;
    align-items: center;
    padding-top: 7px;
  }
}
.logo {
  display: flex;
  justify-content: space-between;
  width: 24.6vw;
}
.logo__image {
  width: 14.583vw;
  height: 6.563vw;
}
.logo__text {
  align-self: center;
  font-size: 0.729vw;
  letter-spacing: 0.05em;
}
@media (max-width: 991px) {
  .logo {
    width: 28%;
  }
  .logo__text {
    font-size: 0.5rem;
  }
}
@media (max-width: 767px) {
  .logo {
    position: absolute;
    top: 0;
    width: 90%;
  }
  .logo__image {
    width: 100px;
    height: auto;
  }
}
.contacts {
  display: flex;
  justify-content: space-between;
  width: 61.3%;
}
.contacts__adress {
  position: relative;
  padding-top: 1.302vw;
  padding-left: 5.938vw;
}
.contacts__adress::before {
  position: absolute;
  content: "";
  left: 0;
  top: 0.625vw;
  width: 5.625vw;
  height: 5.625vw;
  background-image: url("../img/map-min.png");
  background-repeat: no-repeat;
  background-size: cover;
}
.contacts__adress-text {
  margin: 0;
  padding: 0;
  font-size: 0.833vw;
  line-height: 0.938vw;
  letter-spacing: 0.05em;
  font-weight: 700;
}
.contacts__adress-text--map {
  padding-top: 0.156vw;
  padding-bottom: 0.573vw;
  line-height: 1.094vw;
  font-weight: 300;
}
.contacts__adress-button {
  position: relative;
  padding: 0.469vw 0;
  font-size: 0.625vw;
  line-height: 0.001vw;
  letter-spacing: 0.1em;
  color: #ff6078;
  text-transform: uppercase;
  border-bottom: 0.052vw dashed #ff6078;
  outline: none;
  cursor: pointer;
}
.contacts__about {
  padding-top: 0.365vw;
}
.contacts__about-container {
  position: relative;
  padding-left: 2.396vw;
}
.contacts__about-icon-wrapper {
  position: absolute;
  top: 0.208vw;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 1.823vw;
  height: 1.823vw;
  background-color: #ffffff;
  border-radius: 50%;
  box-shadow: 0 0.156vw 1.042vw 0 rgba(68, 68, 68, 0.11);
}
.contacts__icon {
  width: 0.781vw;
  height: 0.781vw;
}
.contacts__about-text {
  font-size: 0.625vw;
  font-weight: 500;
  letter-spacing: 0.08em;
}
.contacts__about-text--phone {
  position: relative;
  padding-left: 0.781vw;
}
.contacts__about-text--phone::before {
  position: absolute;
  content: "";
  top: 0.26vw;
  left: 0;
  width: 0.313vw;
  height: 0.313vw;
  background-color: #27b24b;
  border-radius: 50%;
}
.contacts__about-text--time {
  margin: 0;
  padding: 0.521vw 0;
  font-size: 0.833vw;
  font-weight: 300;
  line-height: 1.094vw;
  letter-spacing: 0.05em;
}
.contacts__about-text--time span {
  font-weight: 700;
}
.contacts__about-phone {
  font-size: 1.25vw;
  font-weight: 500;
  line-height: 0.938vw;
  letter-spacing: 0.025em;
}
.contacts__about-phone:hover {
  color: rgba(40, 40, 40, 0.8);
}
.contacts__callback {
  align-self: center;
}
.contacts__callback-button {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 11.927vw;
  height: 3.125vw;
  font-size: 0.781vw;
  line-height: 1.25vw;
  font-weight: 500;
  letter-spacing: 0.05em;
  color: #1f1d1d;
  background-color: #fefcfb;
  border-radius: 30px;
  box-shadow: 0 0.156vw 0 0 #ebeaea;
  outline: none;
  cursor: pointer;
}
.contacts__callback-button::before {
  position: absolute;
  content: "";
  left: 5%;
  top: 50%;
  transform: translateY(-50%);
  width: 1.042vw;
  height: 1.042vw;
  background-image: url("../img/icon-calendar.png");
  background-repeat: no-repeat;
  background-size: contain;
}
.contacts__callback-button br {
  display: none;
}
.contacts__callback-button:hover {
  color: rgba(40, 40, 40, 0.8);
}
.contacts__adress-map {
  position: absolute;
  top: 1.042vw;
  left: 50%;
  transform: translateX(-50%);
  z-index: 10;
  display: none;
  border-radius: 20px;
  overflow: hidden;
}
.contacts__adress-map iframe {
  width: 26.042vw;
  height: 20.833vw;
}
.contacts__adress-button-wrapper:hover .contacts__adress-map {
  display: block;
  animation: slow-show 0.5s;
}
@media (max-width: 991px) {
  .contacts {
    justify-content: space-around;
    flex-grow: 1;
  }
  .contacts__adress {
    width: 30%;
    padding-left: 0;
  }
  .contacts__adress::before {
    display: none;
  }
  .contacts__adress-text {
    font-size: 11px;
    line-height: 11px;
    text-align: center;
  }
  .contacts__adress-text--map {
    padding-bottom: 2%;
  }
  .contacts__adress-button {
    display: block;
    margin: 0 auto;
    padding-bottom: 5px;
    font-size: 9px;
  }
  .contacts__adress-button:hover .contacts__adress-map {
    display: block;
    animation: none;
  }
  .contacts__adress-map iframe {
    width: 500px;
    height: 300px;
  }
  .contacts__about {
    width: 30%;
    padding-top: 2%;
  }
  .contacts__about-text {
    font-size: 10px;
    text-align: left;
  }
  .contacts__about-text--phone {
    display: none;
  }
  .contacts__about-phone {
    font-size: 14px;
  }
  .contacts__about-icon-wrapper {
    left: -5%;
    top: 0;
    width: 20px;
    height: 20px;
  }
  .contacts__about-icon-wrapper--time {
    display: none;
  }
  .contacts__icon {
    width: 60%;
    height: auto;
  }
  .contacts__callback {
    width: 30%;
  }
  .contacts__callback-button {
    width: 100%;
    height: 38px;
    padding: 0 20%;
    font-size: 13px;
    font-weight: 300;
    line-height: 13px;
  }
  .contacts__callback-button::before {
    width: 30%;
    height: 60%;
  }
  .contacts__callback-button br {
    display: inline;
  }
}
@media (max-width: 767px) {
  .contacts {
    flex-wrap: wrap;
    width: 100%;
  }
  .contacts__adress {
    width: 50%;
  }
  .contacts__adress-map {
    left: 100%;
  }
  .contacts__adress-map iframe {
    width: 320px;
    height: 200px;
  }
  .contacts__callback {
    width: 50%;
    padding-top: 5px;
  }
  .contacts__about {
    order: 1;
    width: auto;
    padding-top: 20px;
  }
  .contacts__about-container {
    padding-left: 25px;
  }
  .contacts__about-phone {
    font-size: 20px;
    line-height: 20px;
  }
  .contacts__about-text {
    padding-top: 10px;
  }
  .contacts__about-icon-wrapper {
    left: 0;
  }
}
.nav {
  padding-top: 0.781vw;
}
.nav__list {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: 3.125vw;
  padding-left: 0.57vw;
  padding-right: 0.57vw;
  background-color: #ffffff;
  border-radius: 0.26vw;
  box-shadow: 0 1.354vw 0 -0.938vw #fef8f5;
}
.nav__link {
  font-size: 0.781vw;
  letter-spacing: 0.05em;
}
.nav__link--active {
  font-weight: 700;
}
.nav__link:hover {
  color: rgba(40, 40, 40, 0.8);
}
.nav__link--active:hover {
  color: #282828;
}
.nav__toggle {
  display: none;
}
@media (max-width: 991px) {
  .nav {
    position: relative;
    margin: 0 auto;
    width: 90%;
  }
  .nav__toggle {
    position: absolute;
    top: 8px;
    right: 10px;
    display: flex;
    justify-content: center;
    width: 45px;
    height: 25px;
    outline: none;
  }
  .nav__toggle::before {
    position: absolute;
    content: "";
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 20px;
    height: 2px;
    background-color: #282828;
    box-shadow: 0 5px 0 0 #282828, 0 -5px 0 0 #282828;
    opacity: 0.7;
  }
  .nav__toggle::after {
    transform: translate(-50%, -50%);
  }
  .nav__toggle--cross::before {
    transform: rotate(45deg);
    width: 20px;
    box-shadow: none;
  }
  .nav__toggle--cross::after {
    position: absolute;
    content: "";
    top: 50%;
    left: 50%;
    transform: rotate(-45deg);
    width: 20px;
    height: 2px;
    background-color: #282828;
    opacity: 0.7;
  }
  .nav__list {
    flex-direction: column;
    height: auto;
    min-height: 25px;
  }
  .nav__item {
    display: none;
    padding: 5px 0;
  }
  .nav__item--active {
    display: block;
  }
  .nav__item--open {
    display: block;
  }
  .nav__link {
    font-size: 14px;
    text-align: left;
  }
}
@media (max-width: 767px) {
  .nav {
    width: 100%;
  }
  .nav__toggle {
    top: 4px;
  }
}
.capture-block {
  position: relative;
  background-image: url("../img/girl-2.png");
  background-repeat: no-repeat;
  background-position: top right;
  background-size: 45.208vw 39.583vw;
